@font-face {
  font-family: "Proxima-Nova";
  font-style: normal;
  font-weight: 300;
  font-display: swap;
  src: local("Proxima-Nova"), url("../fonts/ProximaNova-Light.woff2") format("woff2"), url("../fonts/ProximaNova-Light.woff") format("woff");
}
@font-face {
  font-family: "Proxima-Nova";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local("Proxima-Nova"), url("../fonts/ProximaNova-Regularlt.woff2") format("woff2"), url("../fonts/ProximaNova-Regularlt.woff") format("woff");
}
@font-face {
  font-family: "Proxima-Nova";
  font-style: normal;
  font-weight: 600;
  font-display: swap;
  src: local("Proxima-Nova"), url("../fonts/ProximaNova-Semibold.woff2") format("woff2"), url("../fonts/ProximaNova-Semibold.woff") format("woff");
}
@font-face {
  font-family: "Proxima-Nova";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: local("Proxima-Nova"), url("../fonts/ProximaNova-Bold.woff2") format("woff2"), url("../fonts/ProximaNova-Bold.woff") format("woff");
}
@font-face {
  font-family: "El-Messiri";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: local("Proxima-Nova"), url("../fonts/ElMessiri-Bold.woff2") format("woff2"), url("../fonts/ElMessiri-Bold.woff") format("woff");
}
html {
  min-height: 100%;
}

body {
  background-color: #162831;
  padding: 0;
  margin: 0;
  min-width: 320px;
}

a {
  text-decoration: none;
}

h1,
h2,
h3,
h4,
h5,
h5 {
  margin: 0;
}

.visualy-hidden {
  font-size: 0;
  opacity: 0;
  visibility: hidden;
  width: 0;
  height: 0;
  padding: 0;
}

.main-header__bg-hero {
  margin-top: 20px;
}

@media all and (max-width: 768px) {
  .main-nav {
    background-color: #000000;
  }
}
.main-nav__container {
  margin: 0 auto;
  max-width: 1296px;
  padding: 30px 30px;
  display: grid;
  grid-template-columns: min-content auto min-content;
  align-items: center;
  grid-column-gap: 50px;
}

.main-nav__link-logo {
  height: 60px;
  width: 60px;
}

.main-nav--border-bottom {
  border-bottom: 1px solid rgba(255, 255, 255, 0.5);
  padding: 30px 0;
}

@media all and (max-width: 768px) {
  .main-nav__btn {
    display: none;
  }
}
.main-nav__burger-menu {
  margin-left: auto;
}

.site-list {
  padding: 0;
  margin: 0;
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
  justify-self: center;
}
@media all and (max-width: 768px) {
  .site-list {
    position: absolute;
    top: 120px;
    width: 100%;
    text-align: center;
    background: #306a6a;
    z-index: 1;
    flex-direction: column;
  }
}

.site-list__item {
  margin-right: 50px;
}
@media all and (max-width: 768px) {
  .site-list__item {
    margin-right: 0;
    padding: 29px;
  }
}
.site-list__item:nth-last-child(-n+2) {
  margin-right: 0;
}

.site-list__link {
  font-family: "Proxima-Nova", sans-serif;
  font-size: 14px;
  color: #ffffff;
  font-weight: 400;
  display: block;
  line-height: 19px;
  padding: 5px 0;
}
@media all and (max-width: 768px) {
  .site-list__link {
    font-size: 30px;
  }
}

.site-list__item-btn {
  display: none;
}
@media all and (max-width: 768px) {
  .site-list__item-btn {
    display: block;
    border-top: 1px solid gray;
    padding: 55px 0;
  }
}

.site-list__btn {
  font-size: 30px !important;
}

.btn {
  font-family: "Proxima-Nova", sans-serif;
  font-weight: 600;
  padding: 9px 15px;
  border-radius: 3px;
  white-space: nowrap;
  font-size: 13px;
}
.btn--primary {
  color: #ffffff;
  background-color: #018abe;
}

.title {
  position: relative;
  color: #ffffff;
  font-family: "El-Messiri", sans-serif;
  font-weight: 700;
  font-size: 42px;
  line-height: 59px;
  display: inline-block;
}
.title::before {
  position: absolute;
  content: "";
  width: 28px;
  height: 14px;
  background-color: #018abe;
  border-radius: 0px 50px;
  top: 50%;
  left: -72px;
  transform: translateY(-50%);
}
@media all and (max-width: 768px) {
  .title::before {
    display: none;
  }
}
.title::after {
  position: absolute;
  content: "";
  width: 28px;
  height: 14px;
  background-color: #018abe;
  border-radius: 50px 0px;
  top: 50%;
  right: -72px;
  transform: translateY(-50%);
}
@media all and (max-width: 768px) {
  .title::after {
    display: none;
  }
}

.sessions {
  padding-top: 150px;
}

.sessions__container {
  margin: 0 auto;
  max-width: 1296px;
  padding: 0 30px;
  text-align: center;
}

.sessions__title {
  margin-bottom: 100px;
}

.sessions-card-block {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(2, 258px);
  grid-auto-rows: 258px;
  grid-gap: 24px;
}
@media all and (max-width: 1200px) {
  .sessions-card-block {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media all and (max-width: 768px) {
  .sessions-card-block {
    grid-template-columns: 1fr;
  }
}

.sessions-card-block__item {
  border-radius: 10px;
  background: linear-gradient(0deg, rgba(1, 138, 190, 0.5), rgba(1, 138, 190, 0.5)), url("../img/women-under-water.jpg") no-repeat center/cover;
  text-align: left;
  display: flex;
  align-items: end;
  padding-bottom: 30px;
  padding-left: 40px;
}
.sessions-card-block__item--grid-row {
  grid-row: span 2;
  position: relative;
  padding-bottom: 50px;
  padding-left: 50px;
}
.sessions-card-block__item--grid-row::before {
  position: absolute;
  content: "";
  width: 100%;
  height: 7px;
  bottom: 0;
  left: 0;
  background-color: #018abe;
  border-radius: 0px 0px 10px 10px;
}
@media all and (max-width: 768px) {
  .sessions-card-block__item--grid-row {
    grid-row: auto;
    grid-row: auto;
  }
}
@media all and (max-width: 768px) {
  .sessions-card-block__item {
    padding: 10px 20px;
  }
}

.sessions-card-block__title {
  font-family: "El-Messiri", sans-serif;
  font-weight: 700;
  color: #ffffff;
  font-size: 24px;
  line-height: 34px;
}
@media all and (max-width: 768px) {
  .sessions-card-block__title {
    font-size: 20px;
  }
}

.burger-menu {
  display: none;
}
@media all and (max-width: 768px) {
  .burger-menu {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 40px;
    width: 50px;
  }
}

.burger-menu__item {
  display: none;
}
@media all and (max-width: 768px) {
  .burger-menu__item {
    display: block;
    height: 4px;
    width: 100%;
    background-color: #ffffff;
  }
}

.bg-hero {
  padding: 0 30px;
}

.bg-hero__container {
  margin: 0 auto;
  max-width: 1296px;
  padding-top: 136px;
  background: linear-gradient(0deg, rgba(152, 201, 220, 0.5), rgba(152, 201, 220, 0.5)), url("../img/entry-img.jpg") no-repeat center/cover;
  border: 1px solid #000000;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 10px;
  text-align: center;
  min-height: 600px;
  box-sizing: border-box;
}
@media all and (max-width: 768px) {
  .bg-hero__container {
    min-height: 370px;
    padding-top: 95px;
  }
}

.bg-hero__title {
  position: relative;
  color: #ffffff;
  font-family: "El-Messiri", sans-serif;
  font-weight: 700;
  font-size: 24px;
  line-height: 34px;
  margin-bottom: 40px;
  display: inline-block;
}
.bg-hero__title::before {
  position: absolute;
  content: "";
  width: 28px;
  height: 14px;
  background-color: #ffffff;
  border-radius: 0px 50px;
  top: 50%;
  left: -94px;
  transform: translateY(-50%);
}
@media all and (max-width: 768px) {
  .bg-hero__title::before {
    display: none;
  }
}
.bg-hero__title::after {
  position: absolute;
  content: "";
  width: 28px;
  height: 14px;
  background-color: #ffffff;
  border-radius: 50px 0px;
  top: 50%;
  right: -94px;
  transform: translateY(-50%);
}
@media all and (max-width: 768px) {
  .bg-hero__title::after {
    display: none;
  }
}
@media all and (max-width: 768px) {
  .bg-hero__title {
    font-size: 20px;
  }
}

.bg-hero__main-title {
  color: #ffffff;
  font-size: 64px;
  font-family: "El-Messiri", sans-serif;
  line-height: 90px;
  font-weight: 700;
}
@media all and (max-width: 768px) {
  .bg-hero__main-title {
    line-height: 50px;
    font-size: 35px;
  }
}

.reviews {
  margin-top: 150px;
}

.reviews__container {
  text-align: center;
  max-width: 1736px;
  margin: 0 auto;
}

.slick-arrow {
  display: none;
}

.carousel__item {
  text-align: left;
  background-color: #018abe;
}

.carousel__quote {
  margin: 0;
}